@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400&display=swap);
canvas {
  width: 100%;
  height: 100%;
}

.polygon-name-hide {
  display: none;
}
.polygon-name-show {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.polygon-name-show span {
  position: absolute;
  /*  -webkit-animation: slit-in-horizontal 0.45s;
  animation: slit-in-horizontal 0.45s;*/
  top: 40%;
  left: 0;
  width: 100%;
  height: 25%;
  font-family: 'YouthTouch';
  font-size: 60px;
  text-align: center;
}

.dark-chocolate {
  color: var(--dark-chocolate);
}
.chocolate {
  color: var(--chocolate);
}
.sand {
  color: var(--sand);
}
.camel {
  color: var(--camel);
}

.tracking-in-expand {
  -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1)
    both;
  animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}

.landing {
  /* background: #a27035; */
  background-image: radial-gradient(
    circle,
    #ddca7d,
    #cfb369,
    #c09c56,
    #b18645,
    #a27035
  );
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.welcome-container {
  box-sizing: border-box;
  display: flex;
  position: relative;
  top: 30px;
  justify-content: center;
  color: var(--dark-chocolate);
}
.welcome-container .greeting {
  font-family: 'YouthTouch';
  display: block;
  text-align: center;
  font-size: 50px;
}
.welcome-container .name {
  font-family: 'Classico';
  display: block;
  text-align: center;
  font-size: 100px;
}
.welcome-container .message {
  font-family: 'YouthTouch';
  display: block;
  text-align: center;
  font-size: 50px;
}

.polygons-container {
  display: flex;
  position: relative;
  justify-content: space-around;
  align-self: center;
}

.landing-item {
  display: flex;
}

.bounce-in-top1 {
  -webkit-animation: bounce-in-top 1.5s 0.5s both;
  animation: bounce-in-top 1.5s 0.5s both;
}
.bounce-in-top2 {
  -webkit-animation: bounce-in-top 1.5s 0.7s both;
  animation: bounce-in-top 1.5s 0.7s both;
}
.bounce-in-top3 {
  -webkit-animation: bounce-in-top 1.5s 1s both;
  animation: bounce-in-top 1.5s 1s both;
}
.bounce-in-top4 {
  -webkit-animation: bounce-in-top 1.5s 1.3s both;
  animation: bounce-in-top 1.5s 1.3s both;
}

.fade-in {
  -webkit-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) 3s both;
  animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) 3s both;
}
@media screen and (max-width: 500px) {
  .landing {
    /* background: #a27035; */
    background-image: linear-gradient(
      to top,
      #ddca7d,
      #cfb369,
      #c09c56,
      #b18645,
      #a27035
    );
    width: 100%;
  }
  .welcome-container .greeting,
  .welcome-container .message {
    font-size: 40px;
  }
  .welcome-container .name {
    font-size: 60px;
  }
  .polygons-container {
    display: grid;
    grid-template-columns: 2fr 2fr;
    grid-template-rows: 2fr 2fr;
    grid-gap: 1px 1px;
    gap: 1px 1px;
  }
  /*   .polygons-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
  } */
}

@font-face {
  font-family: 'Classico';
  src: local('Classico'), url(/static/media/Classico-free.d8c96801.otf) format('opentype');
}

@font-face {
  font-family: 'MightyBruch';
  src: local('MightyBruch'),
    url(/static/media/MightyBrushDemo.f3b83041.ttf) format('truetype');
}
@font-face {
  font-family: 'YouthTouch';
  src: local('YouthTouch'),
    url(/static/media/YouthTouchDemoRegular-4VwY.63d75a14.ttf) format('truetype');
}

:root {
  --dark-chocolate: #241f19;
  --chocolate: #533e2d;
  --caramel: #a27035;
  --camel: #b88b4a;
  --sand: #ddca7d;
}

html,
body,
#root,
.App {
  max-height: 100vh;
  max-width: 100vw;
  min-height: 100vh;
  min-width: 100vw;
}
section {
  display: block;
}

